<template><section><h1>DataPicker 日期选择框</h1>
<template>
	<coding title="基本" content="最简单的用法" :code="code1" lang="html">
		<y-date-picker placement="bottom-left" :offset="-22"></y-date-picker>	
		<y-month-picker placement="bottom-left" :offset="-22"></y-month-picker>	
	</coding>
	<coding title="限制" content="限制日期" :code="code2" lang="html">
		<y-date-picker placement="bottom-left" :offset="-22" :disabled-date="{
				star: [2016,12,20],
				end: [2017,2,1]
			}"></y-date-picker>	
	</coding>
	<coding title="国际" content="国际化" lang="html" :code="code3">
		<y-date-picker placement="bottom-left" :offset="-22" en=""></y-date-picker>	
	</coding>
</template>

<h2>参数设置</h2>
<table>
<thead>
<tr>
<th>属性</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
<th>可选值</th>
</tr>
</thead>
<tbody>
<tr>
<td>disabledDate</td>
<td>选择区间</td>
<td><code>Object</code></td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>en</td>
<td>英文</td>
<td><code>Boolean</code></td>
<td><code>false</code></td>
<td>-</td>
</tr>
</tbody>
</table>
</section></template>

<script>
export default {
	data () {
		return {
			code1: 
`<y-date-picker placement="bottom-left" :offset="-22"></y-date-picker>	
<y-month-picker placement="bottom-left" :offset="-22"></y-month-picker>	`,
			code2: 
`<y-date-picker 
	placement="bottom-left" 
	:offset="-22"
	:disabled-date="{
		star: [2016,12,20],
		end: [2017,2,1]
	}"
	:code="code1"
	lang="html"
></y-date-picker>`,
			code3:
`<y-date-picker 
	placement="bottom-left" 
	:offset="-22"
	en
></y-date-picker>`
		}
	}
}
</script>


